.button {
    margin: 0 10px;
    padding: 10px;
    font-size: 14px;
    color: #6c6777;
    background-color: #fff;
    border: none;
    cursor: pointer;
    width: 100px;
    border-radius: 5px;
    margin-top: 10px;
}
  
.button.selected {
    color: #FFF;
    background-color: #34495e;
}
  